	module.exports = {"pathContext":{"docHtml":"<h1>mime-db</h1>\n<p><a href=\"https://npmjs.org/package/mime-db\"><img src=\"https://img.shields.io/npm/v/mime-db.svg\" alt=\"NPM Version\"></a>\n<a href=\"https://npmjs.org/package/mime-db\"><img src=\"https://img.shields.io/npm/dm/mime-db.svg\" alt=\"NPM Downloads\"></a>\n<a href=\"http://nodejs.org/download/\"><img src=\"https://img.shields.io/node/v/mime-db.svg\" alt=\"Node.js Version\"></a>\n<a href=\"https://travis-ci.org/jshttp/mime-db\"><img src=\"https://img.shields.io/travis/jshttp/mime-db/master.svg\" alt=\"Build Status\"></a>\n<a href=\"https://coveralls.io/r/jshttp/mime-db?branch=master\"><img src=\"https://img.shields.io/coveralls/jshttp/mime-db/master.svg\" alt=\"Coverage Status\"></a></p>\n<p>This is a database of all mime types.\nIt consists of a single, public JSON file and does not include any logic,\nallowing it to remain as un-opinionated as possible with an API.\nIt aggregates data from the following sources:</p>\n<ul>\n<li><a href=\"http://www.iana.org/assignments/media-types/media-types.xhtml\">http://www.iana.org/assignments/media-types/media-types.xhtml</a></li>\n<li><a href=\"http://svn.apache.org/repos/asf/httpd/httpd/trunk/docs/conf/mime.types\">http://svn.apache.org/repos/asf/httpd/httpd/trunk/docs/conf/mime.types</a></li>\n<li><a href=\"http://hg.nginx.org/nginx/raw-file/default/conf/mime.types\">http://hg.nginx.org/nginx/raw-file/default/conf/mime.types</a></li>\n</ul>\n<h2>Installation</h2>\n<pre><code class=\"language-bash\">npm install mime-db\n</code></pre>\n<h3>Database Download</h3>\n<p>If you're crazy enough to use this in the browser, you can just grab the\nJSON file using <a href=\"https://rawgit.com/\">RawGit</a>. It is recommended to replace\n<code>master</code> with <a href=\"https://github.com/jshttp/mime-db/tags\">a release tag</a> as the\nJSON format may change in the future.</p>\n<pre><code>https://cdn.rawgit.com/jshttp/mime-db/master/db.json\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var db = require('mime-db');\n\n// grab data on .js files\nvar data = db['application/javascript'];\n</code></pre>\n<h2>Data Structure</h2>\n<p>The JSON file is a map lookup for lowercased mime types.\nEach mime type has the following properties:</p>\n<ul>\n<li>\n<p><code>.source</code> - where the mime type is defined.\nIf not set, it's probably a custom media type.</p>\n<ul>\n<li><code>apache</code> - <a href=\"http://svn.apache.org/repos/asf/httpd/httpd/trunk/docs/conf/mime.types\">Apache common media types</a></li>\n<li><code>iana</code> - <a href=\"http://www.iana.org/assignments/media-types/media-types.xhtml\">IANA-defined media types</a></li>\n<li><code>nginx</code> - <a href=\"http://hg.nginx.org/nginx/raw-file/default/conf/mime.types\">nginx media types</a></li>\n</ul>\n</li>\n<li><code>.extensions[]</code> - known extensions associated with this mime type.</li>\n<li><code>.compressible</code> - whether a file of this type can be gzipped.</li>\n<li><code>.charset</code> - the default charset associated with this type, if any.</li>\n</ul>\n<p>If unknown, every property could be <code>undefined</code>.</p>\n<h2>Contributing</h2>\n<p>To edit the database, only make PRs against <code>src/custom.json</code> or\n<code>src/custom-suffix.json</code>.</p>\n<p>The <code>src/custom.json</code> file is a JSON object with the MIME type as the keys\nand the values being an object with the following keys:</p>\n<ul>\n<li><code>compressible</code> - leave out if you don't know, otherwise <code>true</code>/<code>false</code> for\nif the data represented by the time is typically compressible.</li>\n<li><code>extensions</code> - include an array of file extensions that are associated with\nthe type.</li>\n<li><code>notes</code> - human-readable notes about the type, typically what the type is.</li>\n<li><code>sources</code> - include an array of URLs of where the MIME type and the associated\nextensions are sourced from. This needs to be a <a href=\"https://en.wikipedia.org/wiki/Primary_source\">primary source</a>;\nlinks to type aggregating sites and Wikipedia are <em>not acceptible</em>.</li>\n</ul>\n<p>To update the build, run <code>npm run build</code>.</p>\n<h2>Adding Custom Media Types</h2>\n<p>The best way to get new media types included in this library is to register\nthem with the IANA. The community registration procedure is outlined in\n<a href=\"http://tools.ietf.org/html/rfc6838#section-5\">RFC 6838 section 5</a>. Types\nregistered with the IANA are automatically pulled into this library.</p>","docPath":"argo-ci/node_modules/mime-db/readme","proj":"argo-ci"}}

